.catalog-card {
  padding-top: 40px;
  padding-bottom: 80px;

}

.catalog-card__content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media screen and (max-width: @1024) {
    flex-direction: column;
    align-items: center;
  }
}

.catalog-card__breadcrumbs {
  margin-bottom: 48px;
}

.catalog-card__gallery {
  max-width: 680px;
}

.catalog-card__txt {
  max-width: 544px;

  @media screen and (max-width: @1024) {
    max-width: 680px;
    margin-top: 60px;
  }
}

.catalog-card__title {
  font-size: 40px;
  line-height: 58px;
  font-weight: 500;
  font-family: @jost;
  margin-bottom: 4px;
  margin-top: 0;
}

.catalog-card__price {
  font-size: 28px;
  line-height: 32px;
  font-family: @roboto;
  font-weight: 500;
  margin-bottom: 40px;
}

.catalog-card__settings {
  margin-bottom: 40px;
}

.catalog-card__settings-row {
  display: flex;
  align-items: center;
  margin-bottom: 17px;
}

.catalog-card__settings-txt {
  font-size: 12px;
  line-height: 14px;
  font-family: @roboto;
  color: @black;
  opacity: 0.5;
  min-width: 60px;
  padding-right: 12px;
}

.catalog-card__setting--color {
  display: flex;
}

.catalog-card__color-btn {
  display: block;
  width: 16px;
  height: 16px;
  border: none;
  padding: 0;
  background-color: @gray;
  border-radius: 50%;
  margin-left: 12px;

  &--yellow {
    background-color: @yellow;
  }

  &--brown {
    background-color: brown;
  }

  &--black {
    background-color: @black;
  }
}

.catalog-card__settings-dscr {
  margin-bottom: 70px;
}

.catalog-card__count {
  height: 60px;
  border: 1px solid @gray;
  display: inline-flex;
  width: auto;
}

.catalog-card__input {
  min-height: 58px;
  font-size: 16px;
  line-height: 19px;
  color: @black;
  min-width: 60px;
  padding: 5px;
  text-align: center;
  outline: none;
  border: none;
  border-right: 1px solid @gray;
}

.catalog-card__btns {
  display: flex;
  flex-direction: column;
}

.catalog-card__btn {
  font-size: 16px;
  line-height: 19px;
  color: @black;
  padding-top: 3px;
  font-weight: 500;
  padding-bottom: 3px;
  padding-left: 9px;
  padding-right: 9px;
  background: transparent;
  border: none;
  height: 30px;
  cursor: pointer;
  transition: all ease-in 0.2s;

  &:hover {
    color: @orange;
    background-color: @gray;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }
}

.catalog-card__btn--plus {
  height: 29px;
  border-bottom: 1px solid @gray;
}

.catalog-card__btn-submit {
  border: none;
}

.catalog-card__dscr {
  width: 100%;
  max-width: 802px;
  margin-top: 72px;

  @media screen and (max-width: @1024) {
    max-width: 680px;
  }
}

.catalog-card__dscr-btns-wrap {
  border-bottom: 1px solid @gray;
  padding-bottom: 8px;

  .catalog-card__dscr-btn {
    background-color: transparent;
    font-size: 20px;
    line-height: 28px;
    color: @black;
    opacity: 0.5;
    border: none;
    margin-right: 72px;

    @media screen and (max-width: @576) {
      margin-right: 22px;
    }
  }

  .catalog-card__dscr-btn--active {
    font-weight: 500;
    opacity: 1;
  }
}

.catalog-card__dscr-txt {
  padding-top: 17px;
  display: none;
  min-height: 154px;
}
