.form {
  width: 100%;
  max-width: 652px;
  display: flex;
  flex-direction: column;
}

.form__title {
  display: block;
  font-family: @jost;
  font-size: 28px;
  font-weight: 500;
  line-height: 40px;
  margin-bottom: 44px;
  margin-top: 0;

  @media screen and (max-width: @1024) {
    margin-bottom: 24px;
  }
}

.form__input {
  font-size: 16px;
  line-height: 19px;
  color: @black;
  padding-top: 16px;
  padding-bottom: 16px;
  border: none;
  border-bottom: 1px solid @gray;
  margin-bottom: 36px;
  outline: none;
  opacity: 0.5;

  @media screen and (max-width: @1024) {
    margin-bottom: 14px;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }

  &::placeholder {
    font-size: 16px;
    line-height: 19px;
    color: @black;
    opacity: 0.5;
  }
}

textarea {
  resize: none;
  font-size: 16px;
  line-height: 19px;
  color: @black;
  padding-top: 16px;
  padding-bottom: 16px;
  border: none;
  border-bottom: 1px solid @gray;
  margin-bottom: 36px;
  outline: none;
  opacity: 0.3!important;

  :hover {
    opacity: 0.8!important;
  }

  &:active,
  &:focus {
    opacity: 0.6!important;
  }
}

.form__submit {
  max-width: 265px;
  border: none;
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 16px;

  @media screen and (max-width: @1024) {
    margin-top: 8px;
  }
}
