.cart {}

.cart__main-wrapper {
  padding-top: 100px;

  @media screen and (max-width: @768) {
    overflow-y: scroll;
    width: 100vw;
  }
}

.cart__table {
  width: 100%;
  border: 1px solid @gray;
  margin-bottom: 80px;

  @media screen and (max-width: @768) {
    overflow-x: scroll;
    width: 100vw;
    margin-bottom: 40px;
  }
}

.cart__table-row {
  display: flex;
  align-items: center;
  border-bottom: 1px solid @gray;
  padding-top: 16px;
  padding-bottom: 16px;

  &:last-child {
    border-bottom: none;
  }

  td {
    &:first-child {
      width: 358px;

      @media screen and (max-width: @1280) {
        width: 300px;
      }

      @media screen and (max-width: @576) {
        width: 250px;
      }
    }

    &:nth-child(2) {
      width: 245px;

      @media screen and (max-width: @1280) {
        width: 225px;
      }

      @media screen and (max-width: @576) {
        width: 200px;
      }
    }

    &:nth-child(3) {
      width: 200px;

      @media screen and (max-width: @1280) {
        width: 180px;
      }

      @media screen and (max-width: @576) {
        width: 160px;
      }
    }

    &:nth-child(4) {
      width: 283px;

      @media screen and (max-width: @1280) {
        width: 203px;
      }

      @media screen and (max-width: @576) {
        width: 180px;
      }
    }

    &:last-child {
      flex-grow: 1;

      @media screen and (max-width: @1280) {
        text-align: center;
      }

      @media screen and (max-width: @1280) {
        text-align: right;
        padding-right: 20px;
      }
    }
  }
}

.cart__table-row--title {
  td {
    font-size: 16px;
    line-height: 23px;
    font-weight: 500;
    font-family: @jost;
  }
}

.cart__table-row--item {
  td {
    font-size: 20px;
    line-height: 28px;
    font-weight: 500;
    font-family: @jost;

    input {
      font: inherit;
      color: inherit;
    }

    &:first-child {
      display: flex;
      align-items: center;

      .cart__btn--close {
        margin-right: 54px;
        margin-left: 40px;
      }
    }
  }
}

.cart__table-row--final {
  justify-content: flex-end;

  @media screen and (max-width: @768) {
    flex-direction: row-reverse;
    // justify-content: flex-start;
    padding-left: 20px;
  }

  td {
    &:first-child {
      width: auto;

      .cart__input-code {
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
        letter-spacing: 0.08em;
        text-align: center;
        color: @gray;
        padding: 21px 8px;
        border: 1px solid @gray;

        @media screen and (max-width: @576) {
          width: 168px;
        }

        &::placeholder {
          color: @black;
          opacity: 0.5;
        }
      }
    }

    &:last-child {
      flex-grow: 0;
      height: 65px;
      margin-left: 20px;
      margin-right: 20px;
      width: 280px;

      @media screen and (max-width: @576) {
        width: 220px;
      }

      .cart__btn {
        display: block;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
        letter-spacing: 0.08em;
        text-align: center;
        color: @black;
        padding: 22px 52px;
        white-space: nowrap;
        background-image: @gradient;
        height: 100%;

        @media screen and (max-width: @576) {
          padding-left: 22px;
          padding-right: 22px;
        }
      }
    }
  }
}

.cart__count {
  height: 60px;
  border: 1px solid @gray;
  display: inline-flex;
  width: auto;
}

.cart__input {
  min-height: 58px;
  font-size: 16px;
  line-height: 19px;
  color: @black;
  min-width: 60px;
  padding: 5px;
  text-align: center;
  outline: none;
  border: none;
  border-right: 1px solid @gray;
}

.cart__btns {
  display: flex;
  flex-direction: column;
}

.cart__btn {
  font-size: 16px;
  line-height: 19px;
  color: @black;
  padding-top: 3px;
  font-weight: 500;
  padding-bottom: 3px;
  padding-left: 9px;
  padding-right: 9px;
  background: transparent;
  border: none;
  height: 30px;
  cursor: pointer;
  transition: all ease-in 0.2s;

  &:hover {
    color: @orange;
    background-color: @gray;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }
}

.cart__btn--plus {
  height: 29px;
  border-bottom: 1px solid @gray;
}

.cart__btn-submit {
  border: none;
}
