.gallery {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 680px;
  min-height: 505px;

  @media screen and (max-width: @576) {
    width: 100%;
    min-width: 100%;
    height: auto;
    min-height: auto;
  }
}

.gallery__pictures {
  display: flex;

  @media screen and (max-width: @576) {
    flex-direction: column;
    width: 100%;
    overflow-y: auto;
    overflow-x: scroll;
  }
}

.gallery__picture-full {
  margin-bottom: 0;
  background-color: @gray;
  height: 505px;

  @media screen and (max-width: @576) {
    width: 100%;
    height: auto;

    img {
      width: 100%;
      height: auto;
    }
  }
}

.gallery__picture-full img {
  display: block;
}

.gallery__picture-previews {
  display: flex;
  width: 170px;
  height: 505px;
  overflow-y: scroll;
  margin: 0;
  margin-right: 10px;
  flex-direction: column;
  list-style-type: none;
  padding: 0;
  justify-content: space-between;
  order: -1;
  scrollbar-color: @black @gray;
  scrollbar-width: thin;

  @media screen and (max-width: @576) {
    flex-direction: row;
    height: 133px;
    width: 100%;
    overflow-y: auto;
    overflow-x: scroll;
    margin-bottom: 10px;
  }

  &::-webkit-scrollbar {
    width: 3px;
    height: 3px;
  }

  &::-webkit-scrollbar-button {
  }

  &::-webkit-scrollbar-track {
    background-color: #999;
  }

  &::-webkit-scrollbar-track-piece {
    background-color: @gray;
  }

  &::-webkit-scrollbar-thumb {
    background-color: @black;
    border-radius: 3px;
  }

  &::-webkit-scrollbar-corner {
    background-color: #999;
  }

  &::-webkit-resizer {
    background-color: #666;
  }
}

.gallery__picture-preview {
  background-color: @gray;
  position: relative;
  padding: 0;
  border: 0;
  border-radius: 0;
  outline: none;
  top: 0;
  opacity: 1;
  transition: top 0.2s, box-shadow 0.2s, filter 0.2s, opacity 0.2s;
  margin-bottom: 20px;
  width: 155px;

  &:last-child {
    margin-bottom: 0;
  }

  @media screen and (max-width: @576) {
    width: 120px;
    height: 120px;
    margin-right: 10px;
    margin-bottom: 10px;
  }
}

.gallery__picture-preview:hover {
  outline-offset: -3px;
}

.gallery__picture-preview:focus {
  top: -5px;
  box-shadow: 0 3px 10px 2px rgba(0, 1, 1, 0.2);
  opacity: 1;
}

.gallery__picture-preview img {
  display: block;
  object-fit: cover;
  width: 155px;
  height: 155px;

  @media screen and (max-width: @576) {
    width: 120px;
    height: 120px;
  }
}
