.background-block {
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  min-height: 380px;
  padding-top: 32px;
  padding-bottom: 32px;
  background-color: @gray;

  @media screen and (max-width: @1280) {
    background-size: cover;
  }
}

.background-block--contacts {
  background-image: url("../img/contacts-bg.jpg");
}

.background-block--faq {
  background-image: url("../img/faq-bg.jpg");
}

.background-block--del-pay {
  background-image: url("../img/del-pay-bg.jpg");
}

.background-block--about {
  background-image: url("../img/about-bg.jpg");
}

.background-block--sales-page {
  background-image: url("../img/sales-bg.jpg");
}

.background-block__breadcrumbs {
  margin-bottom: 95px;
}

.background-block__title {
  font-size: 62px;
  line-height: 89px;
  font-family: @jost;
  color: @black;
  font-weight: 400;
  margin: 0;

  @media screen and (max-width: @576) {
    font-size: 36px;
    line-height: 52px;
    margin-bottom: 15px;
  }
}

