.news-page__content {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  padding-bottom: 120px;

  @media screen and (max-width: @768) {
    padding-top: 40px;
    padding-bottom: 60px;
    flex-direction: column;
  }
}

.news-page__left-column {
  max-width: 845px;

  @media screen and (max-width: @1200) {
    max-width: 745px;
  }

  @media screen and (max-width: @1024) {
    max-width: 645px;
  }

  @media screen and (max-width: @768) {
    max-width: 100%;
    margin-bottom: 60px;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
    margin-bottom: 30px;
  }

  .news-page__date {
    font-family: @jost;
    font-size: 16px;
    font-weight: 400;
    line-height: 23px;
    opacity: 0.5;
    margin: 0;
  }

  .news-page__title {
    font-family: @jost;
    font-size: 40px;
    font-weight: 500;
    line-height: 58px;
    margin-bottom: 10px;
    margin-top: 0;

    @media screen and (max-width: @768) {
      font-size: 32px;
      line-height: 50px;
    }
  }

  .newx-page__txt {
    line-height: 25px;
  }
}

.news-page__right-column {
  max-width: 388px;

  @media screen and (max-width: @1024) {
    max-width: 344px;
  }

  .search-block__search-form {
    margin-bottom: 68px;
    border-bottom: 1px solid @gray;
  }

  .news-page__mini-title {
    display: block;
    font-family: @jost;
    font-size: 20px;
    font-weight: 500;
    line-height: 29px;
    margin-bottom: 16px;
  }
}

.news-page__hashtags {
  margin-bottom: 40px;
}

.news-page__hashtags-list {
  display: flex;
  flex-wrap: wrap;
}

.news-page__hashtags-item {
  margin-right: 12px;
  margin-bottom: 12px;
}

.news-page__hashtag {
  font-size: 12px;
  line-height: 14px;
  display: block;
  padding: 7px 15px;
  border: 1px solid #eaeae8;
}

.news-page__categories {
  margin-bottom: 40px;
}

.news-page__categories-list {
  padding-left: 15px;
  list-style: disc;
}

.news-page__categories-item {
  margin-bottom: 12px;
}
