.comparison__main-wrapper {
  padding-top: 30px;
  padding-bottom: 60px;
}

.comparison__list {
  display: flex;
  align-items: flex-end;
}

.comparison__item {
  // margin-right: 20px;
}

.comparison__item--start {
  min-height: 100%;
}

.comparison__item-img-wrapper {
  width: 306px;
  height: 306px;
  position: relative;
  margin-bottom: 112px;

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }

  @media screen and (max-width: @1200) {
    width: 100%;
    height: auto;
    margin-bottom: 60px;
  }

  @media screen and (max-width: @1024) {
    width: 100%;
    height: auto;
    margin-bottom: 40px;
  }
}

.comparison__btn {
  position: absolute;
  top: 24px;
  left: 24px;
  padding: 0;
  border: none;
  background-color: transparent;

  @media screen and (max-width: @1024) {
    left: 18px;
  }

  @media screen and (max-width: @576) {
    left: 10px;
    top: 10px;
  }
}

.comparison__btn--close {
  position: absolute;
  top: 28px;
  left: auto;
  right: 28px;
  padding: 0;
  border: none;
  background-color: transparent;

  @media screen and (max-width: @1024) {
    right: 18px;
  }

  @media screen and (max-width: @576) {
    right: 10px;
    top: 12px;
  }
}

.comparison__sub-list {
  dt {
    font-size: 28px;
    line-height: 40px;
    font-family: @jost;
    font-weight: 500;
    min-height: 40px;
    margin-bottom: 20px;

    @media screen and (max-width: @768) {
      margin-bottom: 12px;
    }

    &:nth-child(2) {
      margin-top: 60px;

      @media screen and (max-width: @768) {
        margin-top: 40px;
      }
    }

    @media screen and (max-width: @768) {
      font-size: 20px;
      line-height: 32px;
      min-height: 32px;
    }
  }

  dd {
    margin-left: 0;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 20px;

    @media screen and (max-width: @768) {
      margin-bottom: 12px;
    }
  }
}
