.info__content-wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  padding-bottom: 80px;

  @media screen and (max-width: @768) {
    flex-direction: column;
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.info__left-column {
  @media screen and (max-width: @768) {
    margin-bottom: 40px;
  }
}

.info__content-title {
  font-family: @jost;
  font-weight: 400;
  font-size: 28px;
  line-height: 40px;
  margin-bottom: 40px;
}

.info__content-sub-title {
  font-weight: 500;
  margin-bottom: 12px;
}

.info__content-address-list {
  li {
    padding: 7px 0;
    opacity: 0.5;

    a {
      color: @black;

      &:hover,
      &:active,
      &:focus {
        color: @orange;
      }
    }
  }
}

.info__form {
  max-width: 652px;
  width: 100%;

  .form__submit {
    @media screen and (max-width: @576) {
      max-width: 100%;
    }
  }
}

.info__right-column {
  max-width: 652px;
  width: 100%;
}

.info--faq {
  .info__right-column {
    max-width: 692px;
    width: 100%;
  }
}

.info__faq-list {
  @media screen and (max-width: @768) {
    list-style: none;
  }

  li {
    margin-bottom: 32px;

    &::marker {
      display: block;
      font-family: @jost;
      font-size: 28px;
      line-height: 40px;
      font-weight: 500;
    }

    b {
      display: block;
      font-family: @jost;
      font-size: 28px;
      line-height: 40px;
      font-weight: 500;
    }

    p {
      line-height: 25px;
    }
  }
}

.info--del-pay {
  .info__txt {
    h4 {
      margin-top: 0;
      font-family: @jost;
      font-size: 28px;
      font-weight: 500;
      line-height: 40px;
    }

    p.info__txt-slogan {
      font-size: 16px;
      font-weight: 500;
      line-height: 26px;
    }

    ul {
      list-style: disc;
      padding-left: 30px;
      margin-bottom: 32px;

      li {
        line-height: 25px;
      }
    }
  }
}

.info--about {
  .info__content-wrapper {
    flex-direction: row-reverse;

    @media screen and (max-width: @768) {
      flex-direction: column;
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }

  .info__right-column {
    max-width: 866px;
  }

  .info__about-title {
    display: block;
    font-family: @jost;
    font-size: 28px;
    font-weight: 500;
    line-height: 40px;
    margin-bottom: 20px;
  }
}
