.products-list {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;

  @media screen and (max-width: @768) {
    justify-content: space-between;
  }
}

.products-list__item {
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;

  @media screen and (max-width: @1366) {
    width: 24%;
    margin-right: auto;
  }

  @media screen and (max-width: @768) {
    width: 48.5%;
    margin-right: 0;
  }

  @media screen and (max-width: @576) {
    width: 100%;
  }

  &:nth-child(4n) {
    margin-right: 0;
  }
}

.products-list__item-img {
  @media screen and (max-width: @1366) {
    width: 100%;
    height: auto;
  }
}

.products-list__sale-count {
  display: none;
}

.products-list__item--sale {
  .products-list__sale-count {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    display: inline-block;
    vertical-align: middle;
    padding: 6px 14px;
    background: @gradient;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.products-list__item-wrapper-txt {
  text-align: center;
}

.products-list__title {
  margin-top: 16px;
  margin-bottom: 4px;
  font-family: @jost;
  font-weight: 500;
  font-size: 20px;
  line-height: 29px;

  @media screen and (max-width: @1366) {
    font-size: 16px;
    line-height: 19px;
  }
}

.products-list__link-title {
  color: @black;
}

.products-list__size {
  font-family: @jost;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.products-list__price {
  font-weight: 500;
  font-size: 28px;
  line-height: 32px;

  @media screen and (max-width: @1366) {
    font-size: 22px;
    line-height: 26px;
  }
}

.products-list--news {
  .products-list__item--news {
    width: 100%;
    max-width: 406px;
    margin-bottom: 60px;
    margin-right: 33px;

    &:nth-child(3n) {
      margin-right: 0;
    }

    &:nth-child(4n) {
      margin-right: 33px;
    }

    @media screen and (max-width: @1280) {
      max-width: 31%;
    }

    @media screen and (max-width: @1024) {
      max-width: 48%;
      margin-bottom: 40px;
      margin-right: auto!important;
      margin-left: auto!important;
    }

    @media screen and (max-width: @576) {
      max-width: 100%;
      margin-bottom: 40px;
      margin-right: auto!important;
      margin-left: auto!important;
    }

    .products-list__date {
      display: flex;
      flex-direction: column;
      width: 60px;
      padding-top: 8px;
      padding-bottom: 8px;
      background-color: @white;
      position: absolute;
      top: 0;
      left: 32px;
    }

    .products-list__date-month {
      font-family: @jost;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      color: @black;
      opacity: 0.5;
      text-align: center;
    }

    .products-list__date-day {
      font-family: @jost;
      font-size: 28px;
      font-weight: 500;
      line-height: 40px;
      text-align: center;
    }

    .products-list__link-img {
      margin-bottom: 20px;
    }

    .products-list__type {
      display: block;
      font-family: @jost;
      font-size: 16px;
      font-weight: 400;
      line-height: 23px;
    }

    .products-list__title {
      font-family: @jost;
      font-size: 28px;
      font-weight: 500;
      line-height: 40px;
      text-align: left;
    }

    .products-list__dscr {
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;
      text-align: left;
      opacity: 0.5;
    }
  }
}

